.product {
    list-style-type: none;
    display:flex;
    gap: 2rem;
}

.products {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    overflow-x: hidden;
    padding-top: 50px;
    gap: 2rem;
    padding-bottom: 50px;
}

.data {
    display:flex;
    flex-direction: column;
    justify-content: space-evenly;
    gap: 10px;
    padding: 0 20px;
    flex-wrap: wrap;
    font-family: 'DejaVu Serif', 'Times New Roman', Times, serif;
}

.title {
    font-weight: 200;
    font-size: 1em;
}

.description {
    font-size: 0.7em;
}

.price {
    font-size: 0.8em;
}

@media (max-width: 900px) {
    .product {
        max-width: 80%;
        flex-direction: column;
        margin: auto;
    }

    li {
        margin: auto;
    }
}